.card {
    background: #fff;
    border-radius: 2px;
    position: relative;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    margin: 20px;
  }
  
  .card > .img-container {
    overflow: hidden;
    text-align: center;
    background:black;
    width: 400px;
  }
  .card > .img-container > img {
    height: 300px;
    width: 300px;
  }
.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
    text-align: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
  }
.nav-pills {
    position: fixed;
    top: 0;
    left: 0;
    background: #000;
    min-height: 65px;
    width: 100%;
    text-align: center;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: #fff;
    font-size: 2em;
    font-weight: bold;
    z-index: 99;
    white-space: nowrap;
}

